@import '../../../template.scss';

.review {
  @include main-container();
  @media (max-width: 600px) {
	padding: 0;
  }
  border-radius: 25px;
  background-color: bisque;
  padding-top: 165px !important;
  padding-bottom: 117px !important;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.review-section-content {
  display: flex;
  justify-content: space-between;
  margin-top: 51px;
  align-items: end;
  margin-bottom: 112px;

}

.section__sub .section__line{
  background-color: rgba(255,255,255,0.4) !important;
}
.section__sub .section__text , .section__sub .section__number{
  color: rgba(255,255,255,0.7) !important;
}


.review-section-title {
  margin: 0;
  font-size: 40px;
  width: 530px;
  color: #fff;
  overflow: hidden;
}

.review-section-desc-con {
  display: flex;
}

.review-section-description {
  width: 368px;
  font-size: 16px;
  line-height: 25px;
  color: rgba(255,255,255,0.4);
}

.review-section-desc-con svg{
  margin-left: 26px;
}



.review-section-img {
  position: relative;
}
.review-section-img img{
  position: absolute;
  bottom: -329px;
  right: -303px;
}



//SLIDER
.slick-slider {
  display: flex !important;
  justify-content: center;
  transition: width 0.5s ease-in-out, height 0.5s ease-in-out; /* Плавное изменение размеров */
  overflow: visible; /* Убедитесь, что элементы не обрезаются */
  align-items: center;

}

.slick-slider .slick-arrow:nth-child(1){
  margin-right: 27px;
}
.slick-slider .slick-arrow:last-child {
  margin-left: 27px;
}

.slick-track {
  //width: auto !important; /* Автоматическая ширина */
  //transform: none !important; /* Сброс ненужных трансформаций */
  transition: transform 0.5s ease-in-out; /* Плавность переключения */

  height: 445px !important;
}

.slider-item {
  position: relative;
  width: 100% !important;
  max-width: 190px; /* Предельная ширина карточки */
  height: 298px !important;
  border-radius: 12px;
  overflow: hidden;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 23px;
  transition: width 0.5s ease-in-out, height 0.5s ease-in-out; /* Плавность размеров */
}


.slick-initialized .slick-slide {
  transition: 0.5s;
  margin-top: 60px ;
}




.slick-initialized .slick-slide .play-button {
  display: none;

}
.slick-initialized .slick-slide .review-video {
  pointer-events: none;
}

.slick-track .slick-active .play-button{
  display: block;
}
.slick-track .slick-active .review-video{
 pointer-events: all;
}




.slick-track .slick-active {
  width: 100% !important;
  max-width: 285px; /* Предельная ширина карточки */
  height: 445px !important;
  margin-top: 0px ;
  border: 2px solid white;
}

.slider-item .review-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  pointer-events: all; /* Запрещаем взаимодействие с видео */
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, transparent 60%, rgba(0, 0, 0, 0.8) 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  pointer-events: none; /* Чтобы только кнопка срабатывала */
}

.play-button {
  width: 50px;
  height: 50px;
  mask-size: contain;
  -webkit-mask-size: contain;
  pointer-events: all; /* Активируем клики */
  cursor: pointer;
}

.video-info {
  position: absolute;
  bottom: 25px;
  left: 25px;
  color: white;
  font-family: Arial, sans-serif;
  pointer-events: none;

}

.video-info h3 {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
}

.video-info p {
  margin: 13px 0 0;
  font-size: 14px;
  color: rgba(255,255,255, 0.5);
}

.play-button:hover {
  opacity: 0.8;
}



