}
.slick-dots li button {
  height: 7px !important;
  padding: 0 !important;
  width: 15px !important;
}
.slick-dots li button:before {
  line-height: 7px!important;
  height: 7px !important;
  font-size: 10px !important;
  width: 15px !important;
}


.custom-dots-container div {
  position: relative;
}


